/* App.css */
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
  background-color: #0a192f;
  color: #ccd6f6;
}

.app {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.sidebar {
  width: 300px;
  background-color: #0a192f;
  color: #ccd6f6;
  position: fixed;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 40px 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.sidebar.open {
  transform: translateX(0);
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar h1 {
  font-size: 2.2em;
  margin: 0;
  color: #ccd6f6;
}

.sidebar p {
  font-size: 1.2em;
  margin: 10px 0;
}

.sidebar nav {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  width: 100%;
  align-items: center;
}

.sidebar-link {
  color: #64ffda;
  text-decoration: none;
  font-size: 1.2em;
  margin: 10px 0;
  transition: color 0.3s ease;
  width: 100%;
  text-align: center;
}

.sidebar-link:hover {
  color: #ccd6f6;
}

.sidebar-link.active {
  color: #ccd6f6;
  font-weight: bold;
}

.social-icons {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.social-icons a {
  color: #64ffda;
  font-size: 1.5em;
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #ccd6f6;
}

.content-wrapper {
  margin-left: 300px;
  height: 100vh;
  overflow-y: auto;
  width: calc(100% - 300px);
}

.main-content {
  padding: 40px;
  background-color: #0a192f;
}

.section {
  padding: 100px 0;
  border-bottom: 1px solid #64ffda;
}

.content {
  max-width: 800px;
  margin: 0 auto;
  text-align: left;
  position: relative;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.content:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.section h2 {
  color: #64ffda;
  margin-bottom: 20px;
  border-bottom: 2px solid #64ffda;
  padding-bottom: 10px;
}

.section ul {
  list-style: none;
  padding: 0;
}

.section ul li {
  margin-bottom: 10px;
  padding-left: 20px;
  position: relative;
}

.section ul li:before {
  content: 'â€¢';
  position: absolute;
  left: 0;
  color: #64ffda;
  font-size: 1.2em;
}

.job h3 a {
  color: #64ffda;
  text-decoration: none;
}

.job h3 a:hover {
  text-decoration: underline;
}

.job .skills {
  margin-top: 10px;
}

.job .skill-badge {
  display: inline-block;
  background-color: #64ffda;
  color: #0a192f;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.9em;
  margin: 2px;
}

.contact .social-links a {
  margin: 0 10px;
  padding: 10px 20px;
  border: 1px solid #64ffda;
  border-radius: 5px;
  text-decoration: none;
  color: #64ffda;
}

.contact .social-links a:hover {
  background-color: #64ffda;
  color: #0a192f;
}

.sidebar-toggle {
  display: none;
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1001;
  background: none;
  border: none;
  color: #64ffda;
  font-size: 2em;
  cursor: pointer;
}

/* Skills section */
.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.skill-item {
  background-color: #00bcd4; /* More relaxed shade of blue */
  color: #0a192f;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1em;
  margin: 5px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .sidebar {
    width: 250px;
    transform: translateX(-100%);
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .sidebar nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .sidebar-link {
    padding: 10px 20px;
    width: 100%;
    text-align: left;
  }

  .sidebar h1,
  .sidebar p {
    text-align: left;
  }

  .content-wrapper {
    margin-left: 0;
    width: 100%;
  }

  .main-content {
    padding: 20px;
  }

  .sidebar-toggle {
    display: block;
  }
}
